















import useLoader from '_/composables/use-loader';
import { defineComponent, ref } from '@vue/composition-api';
import { QRCodeCanvas } from '@cheprasov/qrcode';
import envs from '@/utils/envs';
// import logo from './ideation.svg';

function getHost(mode: string) {
  if (document.domain === 'localhost') return 'http://localhost:1234';
  const https = 'https://';
  const http = 'http://';
  const protocalMap = {
    [mode]: http,
    uat: https,
    art: https,
  };
  const mg = 'm-';
  const mp = 'm.';
  const pre = {
    uat: mg,
    art: mg,
  };
  const map1 = {
    [mode]: mode,
    staging: 'sit',
    development: 'dev',
    uat: 'uat',
    production: '',
  };
  const map2 = {
    [mode]: 'local',
    staging: 'local',
    development: 'local',
    uat: 'com',
    production: 'com',
  };
  return `${protocalMap[mode] ?? http}${pre[mode] ?? mp}${map1[mode] ? `${map1[mode]}.` : ''}4dshoetech.${map2[mode]}`;
}

export default defineComponent({
  props: {
    id: String,
  },
  setup(props) {
    const qrcode = ref<HTMLDivElement | null>(null);
    useLoader(async (qr) => {
      // @ts-ignore
      const el = qr.value as HTMLDivElement;
      if (el) {
        const host = getHost(envs.VUE_APP_MODE);
        const link = [host, `material-goods-detail?id=${props.id}`].join('/');
        console.log(link);
        // const tImg = new Image();
        // tImg.src = logo;
        // tImg.style.width = '60px';
        // tImg.style.height = '60px';
        const config = {
          // image: {
          //   source: tImg,
          //   width: 70,
          //   height: 70,
          // },
          size: 258,
        };
        const qrCanvas = new QRCodeCanvas(link, config);
        const dataUrl = await qrCanvas.toDataUrl() as string;
        const img = new Image();
        img.crossOrigin = 'anonymous';
        img.src = dataUrl;
        el.appendChild(img);
      }
    }, qrcode);
    return {
      qrcode,
    };
  },
});
